// Variables

$bodyFont: Avenir, Arial, sans-serif;


// Basic Page Styles

html {}

body {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
	background: black;
	font-family: $bodyFont;
}

div {
	position: relative;
	display: block;
	box-sizing: border-box;
	margin: 0;
	padding: 0;
}

.girdWrap {
	position: relative;
	display: block;
	width: 100%;
	margin: 0 auto;
	border: 1px solid pink;
}

header {
	margin: 0 auto;
	padding: 1em 0;
	display: block;
	position: relative;
	width: 100%;
	text-align: center;
	border-bottom: 1px solid #111;

	.titleWrap {
		display: inline-block;
		width: 30%;
		vertical-align: top;
		padding-top: 0em;
		text-align: center;

		h1 {
			font-weight: 900;
			font-size: 3em;
			color: white;
			text-transform: uppercase;
			line-height: .8em;
			text-align: left;
			margin-top: .5em;
		}
	}

	.logoWrap {
		width: 30%;
		display: inline-block;

		svg {
			height: 125px;
		}
	}
}

article {
	margin: 0 auto;
	padding: 1em 0;
	vertical-align: top;
	color: white;

	h1 {
		text-transform: uppercase;
		margin-bottom: 0;
		color: #aaa;
	}

	h2 {
		text-transform: uppercase;
		margin-top: 0;
		font-size: 1em;
	}

	.colTwoThird {
		display: inline-block;
		width: 51%;
		margin-left: 3%;
		margin-right: 6%;
		
	}

	.colOneThird {
		display: inline-block;
		width: 33%;
		height: 60%;
		margin-right: 3%;
		border-left: 1px solid #333;
		vertical-align: top;
		margin-top: 4em;
		padding: 0em 0 0 6%;

		.eventTitle, .eventTime {
			font-size: 1em;

		}

		h1 {
			margin-top: 2em;
		}

		ul {
			padding: .5em 0 0 0;
		}
	}

	ul {
		padding: 2em 1em 1em; 

		li {
			width: 100%;
			color: white;
			list-style: none;
			border-bottom: 1px solid #111;
			padding: 5px 0 30px;
		}
	}

	.eventTitle {
		display: inline-block;
		position: relative;
		float: left;
		text-transform: uppercase;
		font-weight: 900;
		font-size: 1.2em;
	}

	.eventTime {
		display: inline-block;
		position: relative;
		float: right;
		font-weight: 900;
		font-size: 1.2em;
	}
}



// Frame Specific Styles

.keyVisual {
	background: url('http://chamonix.arcteryxacademy.com/wp-content/uploads/2016/01/bg1.jpg');
	background-size: cover;
}

.logoLarge {
	margin: 0 auto;
	text-align: center;
	svg {
		padding: 22% 0 0 0;
		height: 300px;
	}
}